<template>
  <div class="app">
    <Menu mode="horizontal" theme="dark" :active-name="route" @on-select="toUrl">
      <MenuItem name="flowList">
        <Icon type="ios-people"/>
        流程列表
      </MenuItem>
      <MenuItem name="flowDesign">
        <Icon type="ios-paper"/>
        流程设计
      </MenuItem>
    </Menu>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      route: 'flowList'
    }
  },
  watch: {
    $route: {
      handler(newRouter) {
        this.route = newRouter.name
      },
      immediate: true,
    }
  },
  methods: {
    toUrl(url) {
      this.route = url.replace('/', '')
      this.$router.push({path: url});
    }
  }
}
</script>

<style lang="scss">
html, body {
  width: 100%;
  height: 100%;
}

.app {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

</style>



