import axios from "axios";

// axios.defaults.baseURL = 'http://localhost'

export function deploy(params) {
  return axios.post('/easy/process/deploy', params)
}

export function find(params) {
  return axios.get('/easy/process/find', {
    params
  })
}

export function design(sid) {
  return axios.get(`/easy/process/design/${sid}`)
}

export function findById(sid) {
  return axios.get(`/easy/process/detail/${sid}`)
}

export function updateById(params) {
  return axios.post('/easy/process/update', params)
}

export function reset(params) {
  return axios.post('/easy/process/reset', params)
}

export function removeById(sid) {
  return axios.delete(`/easy/process/${sid}`)
}



