import Vue from 'vue'
import Router from 'vue-router'
import FlowList from '@/views/FlowList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'FlowDesign',
      component: FlowList
    },
    {
      path: '/flowDesign',
      name: 'flowDesign',
      component: () => import('@/views/FlowDesign')
    },
    {
      path: '/flowList',
      name: 'flowList',
      component: () => import('@/views/FlowList')
    }
  ]
})



