<template>
  <div>
    <Form :model="searchForm" :label-width="60" style="margin-left: 10px; margin-top: 10px" :inline="true">
      <Form-item label="流程名称">
        <Input v-model="searchForm.title" style="width: 200px"></Input>
        <Button type="primary" @click="loadData">搜索</Button>
      </Form-item>
    </Form>
    <hr style="color: darkgrey;"/>

    <Table border :context="context" :columns="columns" :data="dataList" style="margin-top: 10px"></Table>

    <Modal
      v-model="modal"
      title="编辑流程信息" width="400px">
      <Form :model="form" :label-width="60">
        <FormItem label="名称" class="input-item">
          <Input v-model="form.title" placeholder="" style="width: 300px"></Input>
        </FormItem>
        <FormItem label="失败模式" class="input-item" v-if="">
          <Select v-model="form.backMode" style="width: 300px">
            <Option :value="1">整体失败</Option>
            <Option :value="2">退回上一步</Option>
          </Select>
        </FormItem>
        <FormItem label="描述" class="input-item">
          <Input v-model="form.description" type="textarea" :rows="5" placeholder="" style="width: 300px"></Input>
        </FormItem>
      </Form>

      <div slot="footer">
        <Button type="text" size="large" @click="modal = false">取消</Button>
        <Button type="primary" size="large" @click="handleSubmit">提交</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import {find, removeById, findById, updateById} from "../api/processApi";

export default {
  name: "FlowList",
  data() {
    let vm = this
    return {
      modal: false,
      context: this,
      searchForm: {
        title: ''
      },
      form: {
        sid: null,
        title: '',
        description: '',
        backMode: ''
      },
      columns: [
        {
          type: 'index',
          width: 60,
          align: 'center'
        },
        {
          title: '流程名称',
          key: 'title',
          align: 'center'
        },
        {
          title: '描述',
          key: 'description',
          align: 'center'
        },
        {
          title: '失败模式',
          align: 'center',
          key: 'backMode',
          render(h, params) {
            return h('span', params.row.backMode === 1 ? '整体失败' : '退回上一步')
          }
        },
        {
          title: '创建时间',
          key: 'created',
          align: 'center'
        },
        {
          title: '更新时间',
          key: 'lastUpdated',
          align: 'center'
        },
        {
          title: '操作',
          key: 'action',
          width: 300,
          align: 'center',
          render(h, params, index) {
            return h('div', [
              h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {marginLeft: '5px'},
                  on: {
                    click: () => {
                      vm.$router.push({
                        path: '/flowDesign',
                        query: {
                          sid: params.row.sid
                        }
                      })
                    }
                  }
                }, '设计'
              ),
              h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {marginLeft: '5px'},
                  on: {
                    click: () => {
                      //根据id查询
                      findById(params.row.sid).then(res => {
                        if (res.data.code === 200) {
                          vm.form = res.data.body
                          vm.modal = true
                        } else {
                          vm.$Message.error(res.data.message)
                        }
                      }).catch(error => {
                        this.$Message.error(error)
                      })
                    }
                  }
                }, '编辑'
              ),
              h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  style: {marginLeft: '5px'},
                  on: {
                    click: () => {
                      vm.$Modal.confirm({
                        title: "提示",
                        content: "<p>您确认销毁该流程吗</p>",
                        onOk: () => {
                          removeById(params.row.sid).then(res => {
                            if (res.data.code === 200) {
                              vm.$Message.success('删除成功')
                              vm.loadData()
                            } else {
                              vm.$Message.error(res.data.message)
                            }
                          }).catch(error => {
                            vm.$Message.error(error)
                          })
                        }
                      })
                    }
                  }
                }, '删除'
              ),
            ])
          }
        }
      ],
      dataList: []
    }
  },
  methods: {
    loadData() {
      find(this.searchForm).then(res => {
        this.dataList = res.data.body
      })
    },
    handleSubmit() {
      updateById(this.form).then(res => {
        if (res.data.code === 200) {
          this.$Message.success('修改成功')
          this.loadData()
          this.modal = false
        } else {
          this.$Message.error(res.data.message)
        }
      }).catch(error => {
        this.$Message.error(error)
      })
    }
  },
  created() {
    this.loadData()
  }
}
</script>

<style scoped>
</style>



